
import Image from 'next/image';
import { PRODUCTS_CATALOG, PRODUCT_CATALOG_STRING_DESCRIPTIONS } from '@/lib/constants';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { ProductSuggestions } from '@/components/core/product-suggestions';
import { ArrowLeft, ShoppingCart, Star } from 'lucide-react';
import Link from 'next/link';
import { Separator } from '@/components/ui/separator';
import { Card, CardContent } from '@/components/ui/card';

interface ProductPageProps {
  params: {
    id: string;
  };
}

export async function generateStaticParams() {
  return PRODUCTS_CATALOG.map((product) => ({
    id: product.id,
  }));
}

export default function ProductPage({ params }: ProductPageProps) {
  const product = PRODUCTS_CATALOG.find((p) => p.id === params.id);

  if (!product) {
    return (
      <div className="container mx-auto p-4 text-center">
        <h1 className="text-2xl font-bold">Product not found</h1>
        <Link href="/products">
          <Button variant="link">Go back to products</Button>
        </Link>
      </div>
    );
  }

  return (
    <div className="container mx-auto max-w-5xl py-8 px-4">
      <Link href="/products" className="inline-flex items-center text-sm text-muted-foreground hover:text-foreground mb-6">
        <ArrowLeft className="mr-2 h-4 w-4" />
        Back to Products
      </Link>

      <div className="grid md:grid-cols-2 gap-8 lg:gap-12">
        <Card className="overflow-hidden shadow-lg">
          <CardContent className="p-0">
            <div className="aspect-square relative w-full">
              <Image
                src={product.imageUrl}
                alt={product.name}
                layout="fill"
                objectFit="cover"
                priority
                data-ai-hint={product.dataAiHint}
              />
            </div>
          </CardContent>
        </Card>

        <div className="space-y-6">
          <div>
            <Badge variant="secondary" className="mb-2">{product.category}</Badge>
            <h1 className="text-3xl lg:text-4xl font-bold text-foreground">{product.name}</h1>
            <div className="flex items-center mt-2">
              {[...Array(5)].map((_, i) => (
                <Star key={i} className={`h-5 w-5 ${i < 4 ? 'text-yellow-400 fill-yellow-400' : 'text-muted-foreground'}`} />
              ))}
              <span className="ml-2 text-sm text-muted-foreground">(123 Reviews)</span>
            </div>
          </div>

          <p className="text-2xl font-semibold text-primary">${product.price.toFixed(2)}</p>
          
          <Separator />

          <div>
            <h2 className="text-xl font-semibold text-foreground mb-2">Description</h2>
            <p className="text-muted-foreground leading-relaxed">
              {product.description}
            </p>
          </div>
          
          <Button size="lg" className="w-full md:w-auto">
            <ShoppingCart className="mr-2 h-5 w-5" />
            Add to Cart
          </Button>

        </div>
      </div>
      
      <ProductSuggestions
        currentProductDescription={product.description}
        currentProductId={product.id}
        productCatalogString={PRODUCT_CATALOG_STRING_DESCRIPTIONS}
      />
    </div>
  );
}
